import Vue from 'vue'
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
import VueResource from 'vue-resource'
import App from './App.vue'
import router from './router'

Vue.use(MintUI)
Vue.use(VueResource)

new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



