<template>
	<div id="no1">
		<div class="content">
			<div class="itme clearfix" v-for="i in data" :key="i.id">
				<a :href="'#/no2/'+i.id"><div class="itme_limg">
					<img :src="i.author.avatar_url" alt="" />
				</div></a>
				<div class="itme_font">
					<p class="itme_font_1">{{i.title.substr(0,15)}}</p>
					<p class="itme_font_2 clearfix"><span class="itme_font_21">{{i.author.loginname}}</span><span class="itme_font_22">13小时前</span></p>
				</div>
			</div>

		</div>
	</div>
</template>

<script>
	export default {
		name: 'app',
		data() {
			return {
				data: '',
				a: this.$route.params.id
			}
		},
		methods: {

		},
		watch: {

			'$route.params.id' (a, b) {
			console.log('aaa')
				
				this.a = a
				this.$http.get('https://cnodejs.org/api/v1/topics', {
				params: {
					page: 1,
					tab: this.a,
					limit: 10,
					mdrender: ''
				}
			}).then((data) => {
//				console.log(data)
				this.data = data.data.data
			})
			}
		},
		updated(){
			
		},
		created() {
			
		}
	}
</script>

<style>
	#no1 .nav {
		background-color: #302f2d;
	}
	
	#no1 .nav ul {
		line-height: 1;
	}
	
	#no1 .nav ul li {
		width: 25%;
		line-height: 2.125rem;
		float: left;
		text-align: center;
	}
	
	#no1 .nav ul li a {
		font-size: 0.55rem;
		color: #dcdcdc;
	}
	
	#no1 .nav ul li a:hover {
		color: #FFFFFF;
	}
	
	#no1 .nav ul li a:hover .nav ul li {
		border-bottom: 3px solid #8fb81c;
	}
	
	#no1 .content .itme {
		padding: 0.625rem 0.5rem;
	}
	
	#no1 .itme_font {
		width: 12rem;
	}
	
	#no1 .itme_limg {
		width: 2rem;
		height: 2rem;
		float: left;
		border-radius: 50%;
		margin-right: 0.75rem;
	}
	
	#no1 .itme_limg img {
		width: 2rem;
		height: 2rem;
		border-radius: 50%;
	}
	
	#no1 .itme_font {
		float: left;
		line-height: 1;
	}
	
	#no1 .itme_font_1 {
		font-size: 0.675rem;
		color: #000000;
		margin-bottom: 0.625rem;
	}
	
	#no1 .itme_font_2 .itme_font_21 {
		font-size: 0.5rem;
		color: #5e5e5e;
		float: left;
	}
	
	#no1 .itme_font_2 .itme_font_22 {
		font-size: 0.5rem;
		color: #5e5e5e;
		float: right;
	}
</style>


