import Vue from 'vue'
import Router from 'vue-router'
import no1 from './no1.vue'
import no2 from './no2.vue'

Vue.use(Router)

var routes = [
{
	path: '/no1/:id',
    name: 'index',
    component: no1
},
{
	path: '/no2/:id',
    name: 'index2',
    component: no2
}
]

export default  new Router({
 routes
})



